import { Component , ViewChild } from '@angular/core';
import { ChildComponent } from './child.component'

@Component({
  selector: 'app-parent',
  template:
     `<button (click)="addChange()"> ADD for Child</button>
     	<app-child></app-child>
     `
})

export class ParentComponent {
	@ViewChild(ChildComponent)//khai bao viewchild
	myChild: ChildComponent;//khai bao viewchild
	addChange(){
		this.myChild.value++;
	}
}