import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashbroadComponent } from './dashbroad/dashbroad.component';
import { ListpersonComponent } from './listperson/listperson.component';
import { ServeNotFoundComponent } from './serve-not-found/serve-not-found.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule} from '@angular/common';
    import { from } from 'rxjs';


const appRoutes: Routes = [
    {
        path: '',
        redirectTo: 'login', pathMatch: 'full'
    },
    {
        path: 'login',
        component: LoginComponent
    },
    {
        path: 'dashbroad/:id/:name/:phoneNumber',
        component: DashbroadComponent
    },
    {
        path: 'list',
        component: ListpersonComponent
    },

    {
        path: '**',
        component: ServeNotFoundComponent
    }
]
@NgModule({
    imports:
        [
            RouterModule.forRoot(appRoutes),
            FormsModule,
            ReactiveFormsModule,
            CommonModule
        ],
    declarations:
        [LoginComponent,
            DashbroadComponent,
            ListpersonComponent,
            ServeNotFoundComponent],
    exports: [RouterModule]
})
export class AppRoutingModule { }